	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"html":"<p>class: middle</p>\n<h1>Loops</h1>\n<h2>But first Arrays and Objects</h2>\n<hr>\n<p>class: middle center</p>\n<h1>Arrays</h1>\n<h3>arrays are used to store multiple values in a single variable.</h3>\n<p>.left[</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">var</span> car1 <span class=\"token operator\">=</span> <span class=\"token string\">\"Saab\"</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">var</span> car2 <span class=\"token operator\">=</span> <span class=\"token string\">\"Volvo\"</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">var</span> car3 <span class=\"token operator\">=</span> <span class=\"token string\">\"BMW\"</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h2>]</h2>\n<h1>Lame</h1>\n<hr>\n<p>class: middle center</p>\n<h1>This is way cooler</h1>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">var</span> cars <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">\"Saab\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"Volvo\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"BMW\"</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<hr>\n<p>class: middle center</p>\n<h1>Declaring Arrays with ’[]’</h1>\n<ul>\n<li><code>var cars = new Array(\"Saab\", \"Volvo\", \"BMW\");</code></li>\n<li><code>var person = [];</code></li>\n</ul>\n<hr>\n<p>class: middle</p>\n<p>.center[</p>\n<h1>Access (index into) an Array item</h1>\n<p>]</p>\n<ul>\n<li>Count starts at Zero</li>\n<li>New items are added to the end</li>\n</ul>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">var</span> cars <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Array</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"Saab\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"Volvo\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"BMW\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\ncars<span class=\"token punctuation\">[</span><span class=\"token number\">0</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span> <span class=\"token comment\">//\"Saab\"</span>\ncars<span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span> <span class=\"token comment\">//\"Volvo\"</span>\ncars<span class=\"token punctuation\">[</span><span class=\"token number\">3</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span> <span class=\"token comment\">// ??</span>\n</code></pre>\n      </div>\n<hr>\n<h1>Array Methods</h1>\n<ul>\n<li>length</li>\n<li>push()</li>\n<li>pop()</li>\n<li>indexOf()</li>\n</ul>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array\">Complete Listing</a></p>\n<hr>\n<p>class: middle center</p>\n<h1>So, remember Loops ?</h1>\n<hr>\n<p>class: middle</p>\n<h2>Loop Constructs</h2>\n<ul>\n<li>silly powerful</li>\n<li>for - loops through a block of code a number of times.</li>\n<li>Loops offer a quick and easy way to do something repeatedly. This chapter of the JavaScript Guide introduces the different iteration statements available to JavaScript.</li>\n</ul>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">for</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">var</span> i <span class=\"token operator\">=</span> <span class=\"token number\">0</span><span class=\"token punctuation\">;</span> i <span class=\"token operator\">&lt;</span> <span class=\"token number\">5</span><span class=\"token punctuation\">;</span> i<span class=\"token operator\">++</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>i<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h2><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Guide/Loops_and_iteration\">Details</a></h2>\n<hr>\n<p>Let’s do some!</p>\n<ul>\n<li><a href=\"http://jsbin.com/boyegeq/2/edit?js,console\">Pluralize Animals</a></li>\n<li><a href=\"http://jsbin.com/fagefac/9/edit?js,console\">Empty Array</a></li>\n<li><a href=\"http://jsbin.com/ligivo/1/edit?js,console\">Does Array Contain</a></li>\n</ul>\n<hr>","internal":{"content":"---\ntitle: \"JavaScript Loops\" \ncourseId:  \"CFSG105\"\ncirriculum: \n  - \"Web / Full Stack / Software Development\"\ntype: \"slide\"\ndraft: false\n---\n\nclass: middle\n\n# Loops\n\n## But first Arrays and Objects\n\n---\n\nclass: middle center\n\n# Arrays\n\n### arrays are used to store multiple values in a single variable.\n\n.left[\n\n```js\nvar car1 = \"Saab\";\nvar car2 = \"Volvo\";\nvar car3 = \"BMW\";\n```\n\n## ]\n\n# Lame\n\n---\n\nclass: middle center\n\n# This is way cooler\n\n```js\nvar cars = [\"Saab\", \"Volvo\", \"BMW\"];\n```\n\n---\n\nclass: middle center\n\n# Declaring Arrays with '[]'\n\n* `var cars = new Array(\"Saab\", \"Volvo\", \"BMW\");`\n* `var person = [];`\n\n---\n\nclass: middle\n\n.center[\n\n# Access (index into) an Array item\n\n]\n\n* Count starts at Zero\n* New items are added to the end\n\n```js\nvar cars = new Array(\"Saab\", \"Volvo\", \"BMW\");\ncars[0]; //\"Saab\"\ncars[1]; //\"Volvo\"\ncars[3]; // ??\n```\n\n---\n\n# Array Methods\n\n* length\n* push()\n* pop()\n* indexOf()\n\n[Complete Listing](https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array)\n\n---\n\nclass: middle center\n\n# So, remember Loops ?\n\n---\n\nclass: middle\n\n## Loop Constructs\n\n* silly powerful\n* for - loops through a block of code a number of times.\n* Loops offer a quick and easy way to do something repeatedly. This chapter of the JavaScript Guide introduces the different iteration statements available to JavaScript.\n\n```javascript\nfor (var i = 0; i < 5; i++) {\n  console.log(i);\n}\n```\n\n## [Details](https://developer.mozilla.org/en-US/docs/Web/JavaScript/Guide/Loops_and_iteration)\n\n---\n\nLet's do some!\n\n* [Pluralize Animals](http://jsbin.com/boyegeq/2/edit?js,console)\n* [Empty Array](http://jsbin.com/fagefac/9/edit?js,console)\n* [Does Array Contain](http://jsbin.com/ligivo/1/edit?js,console)\n\n---\n"},"fields":{"slug":"/courses/coding-from-scratch/slides/JS-Loops/"}}}]}},"pathContext":{"slug":"/courses/coding-from-scratch/slides/JS-Loops/"}}

